import { Observable } from 'rxjs';

export class RealTimeMock {
  deviceId;
  bootstrapObserver;
  managedObjObserver;
  observable(channel) {
    if (channel === '/bootstrap') {
      return new Observable(observer => {
        this.bootstrapObserver = observer;
      });
    } else {
      return new Observable(observer => {
        this.managedObjObserver = observer;
      });
    }
  }

  triggerBootstrap(res) {
    this.bootstrapObserver.next(res);
  }
  triggerManagedObject(res) {
    this.managedObjObserver.next(res);
  }
}
